@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
:root {
  --blue-color: #00acdc;
}

* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #000;
  text-decoration: none;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
}

html {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.header__navigation {
  flex-direction: row;
  display: flex;
}

.navigation__item {
  align-items: center;
  height: 60px;
  display: flex;
}

.navigation__link {
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  display: flex;
  position: relative;
}

.navigation__item:not(:last-child) {
  margin-right: 20px;
}

.navigation__link:hover, .navigation__link.is-active {
  color: var(--blue-color);
}

.navigation__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

